.footer {
  flex-shrink: 0;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 78%;
    display: block;
    width: 100%;
    height: 1px;
    background-color: $gray-border;
    opacity: 0.2;

    @media (max-width: $tablet-width) {
      display: none;
    }
  }
}

.footer__wrapper {
  display: grid;
  grid-template-columns: 1fr min-content 1fr 1fr 1fr;
  grid-template-rows: min-content min-content min-content;

  box-sizing: border-box;
  max-width: 1920px;
  margin: 0 auto;
  padding: 65px 13% 14px;

  @media (max-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: min-content min-content min-content;

    padding: 19px 25px 24px;
  }

  @media (max-width: $mobile-width) {
    padding: 46px 15px 26px;
    grid-template-columns: 1fr;
    grid-tempalte-rows: auto;
  }
}

.footer__logo {
  grid-column: 1/3;

  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  min-width: 250px;
  padding: 6px 0 0 62px;

  @media (max-width: $tablet-width) {
    grid-row: auto;
    grid-column: auto;

    padding: 6px 0 26px 68px;
  }

  @media (max-width: $mobile-width) {
    grid-column: auto;
    grid-row: auto;
    justify-self: center;

    margin: 6px 0 0 34px;
  }
}

.footer__social {
  grid-row: 2/3;
  grid-column: 1/3;

  align-self: baseline;

  @media (max-width: $tablet-width) {
    grid-row: auto;
    grid-column: auto;
    justify-self: end;
    align-self: center;
  }

  @media (max-width: $mobile-width) {
    grid-column: auto;
    grid-row: auto;
    justify-self: center;

    padding: 0 0 34px 0;
  }
}

.footer__nav {
  grid-row: 1/3;
  grid-column: 3/5;

  justify-self: start;

  width: 100%;
  margin: 0 0 62px 0;
  padding: 0 212px 0 72px;

  @media (max-width: $tablet-width) {
    grid-row: auto;
    grid-column: auto;

    margin: 0;
    padding: 38px 0 40px 0;
  }

  @media (max-width: $mobile-width) {
    grid-column: auto;
    grid-row: auto;

    width: 100%;
    padding: 45px 0 40px;
  }
}

.footer__contacts {
  grid-row: 1/3;
  padding: 0 0 0 16px;

  @media (max-width: $tablet-width) {
    grid-row: auto;
    grid-column: 2/4;
    justify-self: end;

    padding: 38px 0 0 0;
  }

  @media (max-width: $mobile-width) {
    grid-column: auto;
    grid-row: auto;
    justify-self: start;

    width: 100%;
    padding: 18px 0 0 0;
  }
}

.footer__rights {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.footer__confidentiality {
  @media (max-width: $tablet-width) {
    align-self: center;
  }

  @media (max-width: $mobile-width) {
    grid-column: auto;
    grid-row: auto;
    justify-self: center;

    margin: 40px 0 0 0;
  }
}

.footer__founded {
  grid-column: 2/4;

  @media (max-width: $tablet-width) {
    grid-row: 1/2;
    grid-column: 2/3;
    justify-self: end;
    align-self: center;
  }

  @media (max-width: $mobile-width) {
    grid-column: auto;
    grid-row: 2/3;
    justify-self: center;
    place-self: center center;
  }
}

.footer__copyright {
  @media (max-width: $tablet-width) {
    grid-column: 3/4;
    justify-self: end;
    align-self: center;
  }

  @media (max-width: $mobile-width) {
    grid-column: auto;
    grid-row: auto;
    justify-self: center;
  }
}

.footer h2 {
  margin: 0;
  padding: 0;

  font-family: $font-header;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 107%;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: $dark-gray;
}

.footer__logo svg {
  position: absolute;
  left: 0;
  top: 0;
}

.footer__logo-head {
  font-family: $font-header;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;

  color: $black-font;
}

.footer__logo-text {
  font-family: $font-header;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;

  color: $gray-font;
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;

  @media (max-width: $mobile-width) {
    margin: 10px 0 0 0;
  }
}

.footer__social-item {
  margin: 0 16px 0 0;

  &:last-child {
    margin: 0;
  }

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.footer__nav-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: space-between;

  max-height: 160px;
  margin: 29px 0 0 0;
  padding: 0;
  list-style: none;

  @media (max-width: $tablet-width) {
    max-height: initial;
  }
}

.footer__nav-item {
  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.footer__nav-item a {
  font-family: 'PT Mono';
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 167%;

  color: $dark-gray-op50;
}

.footer__contacts-list {
  margin: 0;
  padding: 0;
  list-style: none;

  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 25px;

  color: $dark-gray-op50;
}

.footer__contacts-item a {
  color: $dark-gray-op50;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.footer__contacts-item p {
  margin: 23px 0 0 0;

  @media (max-width: $mobile-width) {
    margin: 36px 0 0 0;
  }
}


.footer__rights p,
.footer__founded p,
.footer__confidentiality p,
.footer__copyright p {
  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 185%;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $dark-gray-op50;

  @media (max-width: $mobile-width) {
    margin: 5px 0 20px 0;
  }
}
